import { Component, OnInit, ViewChild, ElementRef, Output, EventEmitter, Input, OnChanges, SimpleChanges } from '@angular/core';
import { AreaService } from '../area.service';
import { CourseInfo, CourseNode } from '../course-node';
import { UserData } from '../user-data';
import { JobInfo } from '../job-info';
import { RecommendService } from '../recommend.service';
import { StateService } from '../state.service';
import { EduStructureService } from '../edu-structure.service';

@Component({
  selector: 'app-edu-side-panel',
  templateUrl: './edu-side-panel.component.html',
  styleUrls: ['./edu-side-panel.component.css']
})
export class EduSidePanelComponent implements OnInit,OnChanges {

  private currState: number;

  private areas: string[];
  private selectedArea: string;
  private isAreaSelected = false;
  private dropdownOpened = false;

  private selectedCourseNode: CourseNode;

  private jobInfos: JobInfo[] = [];
  private currJobIndex: number = 0;

  constructor(private areaService: AreaService, private recommendService: RecommendService, private stateService: StateService, private eduService: EduStructureService) {
    this.areas = areaService.AREAS;
  }

  ngOnInit() {
    this.areaService.areaObservable.subscribe(area => {
      this.selectedArea = area;
    });

    this.stateService.stateObservable.subscribe(state => {
      if (state == StateService.OVERVIEW) {
        this.selectedCourseNode = null;
      }
      this.currState = state;
    });

    this.recommendService.recommendJobInfoObservable.subscribe(jobInfos => {
      this.jobInfos = jobInfos;
    });

    this.recommendService.currJobIndexObservable.subscribe(index => {
      this.currJobIndex = index;
    });

    this.recommendService.selectedCourseNodeObservable.subscribe(courseNode => {
      this.selectedCourseNode = courseNode;
    });

    this.eduService.courseNodeObservable.subscribe(courseNode => {
      this.selectedCourseNode = courseNode;
    })
  }

  toggleDropdown() {
    this.dropdownOpened = !this.dropdownOpened;
  }

  ngOnChanges(changes: SimpleChanges) {
  }

  onClickArea(area: string) {
    this.areaService.changeCurrentArea(area);
    this.isAreaSelected = true;
  }

  changeCurrentJobIndex(index: number) {
    this.recommendService.changeJobIndex(index);
  }

}
