import { Component, OnInit } from '@angular/core';
import { UserData } from './user-data';
import { CourseNode } from './course-node';
import { JobInfo } from './job-info';
import { RecommendService } from './recommend.service';
import { SchoolInfo } from './school-info';
import { StateService } from './state.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  currentRecommendResult: JobInfo = null;
  selectedCourseNode: CourseNode;
  schoolInfos: SchoolInfo[] = [];
  userData: UserData;
  currState: number;

  constructor(private recommendService: RecommendService, private stateService: StateService) {
  }

  ngOnInit() {
    this.stateService.stateObservable.subscribe(state => {
      this.currState = state;
    })
  }

  applyUserData(userData: UserData) {
    this.userData = userData;
    this.recommendService.recommendJobs(userData);
  }

}
