import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AreaService {

  AREAS: string[] = [
    'Berlin',
    'Hamburg',
    'Niedersachsen',
    'NRW'
  ];

  LNTLNGS: LatLng[] = [
    {lat: 52.52, lng: 13.40},
    {lat: 53.55, lng: 9.99},
    {lat: 52.63, lng: 9.845},
    {lat: 51.43, lng: 7.661}
  ]

  private areaSource = new BehaviorSubject<string>("Overall");
  areaObservable = this.areaSource.asObservable();
  currentArea = 'Overall';
  constructor() { 
    
  }

  changeCurrentArea(area: string) {
    this.areaSource.next(area);
    this.currentArea = area;
  }

  getCurrAreaLntLng() {
    let currIndex = this.AREAS.indexOf(this.currentArea);
    return this.LNTLNGS[currIndex];
  }

}

export class LatLng {
  lat: number;
  lng: number;
}
