import { Layer } from "./layer";
import { Constants } from "../constants";
import { CourseNode } from "../course-node";

export class Rect {
  public x: number;
  public y: number;
  public width: number;
  public height: number;
  constructor(public courseNode: CourseNode, private layer: Layer, private index: number) {
    let graphWidth = Constants.GRAPH_WIDTH - Constants.GRAPH_MARGIN_SIDE * 2;
    let rectWidth = (graphWidth  - (layer.colCount - 1) * Constants.GRAPH_RECT_MARGIN) / layer.colCount;
    let rectHeight = (this.courseNode.toAge - this.courseNode.fromAge + 1) 
      * Constants.GRAPH_RECT_HEIGHT_UNIT - Constants.GRAPH_RECT_MARGIN;
    this.x = index * (rectWidth + Constants.GRAPH_RECT_MARGIN) + Constants.GRAPH_MARGIN_SIDE;
    this.y = Constants.GRAPH_HEIGHT - layer.from * Constants.GRAPH_RECT_HEIGHT_UNIT;
    this.width = rectWidth
    this.height = rectHeight;
  }

  getStartPos(): number[] {
    return [Math.floor(this.x + this.width / 2), Math.floor(this.y - this.height * 0.74)];
  }

  getEndPos(): number[] {
    return [Math.floor(this.x + this.width / 2), Math.floor(this.y - this.height * 0.26)];
  }

  updateRect(courseNode, layer, index) {
    this.courseNode = courseNode;
    this.updateInOverviewMode(layer, index);
  }

  goToDetailMode() {
    this.width = 200;
    this.x = 70;
  }

  private updateInOverviewMode(layer, index) {
    let graphWidth = Constants.GRAPH_WIDTH - Constants.GRAPH_MARGIN_SIDE * 2;
    let rectWidth = (graphWidth  - (layer.colCount - 1) * Constants.GRAPH_RECT_MARGIN) / layer.colCount;
    let rectHeight = (this.courseNode.toAge - this.courseNode.fromAge + 1) 
      * Constants.GRAPH_RECT_HEIGHT_UNIT - Constants.GRAPH_RECT_MARGIN;
    this.x = index * (rectWidth + Constants.GRAPH_RECT_MARGIN) + Constants.GRAPH_MARGIN_SIDE;
    this.y = Constants.GRAPH_HEIGHT - layer.from * Constants.GRAPH_RECT_HEIGHT_UNIT;
    this.width = rectWidth
    this.height = rectHeight;
  }

}
