import { LayoutManager } from "./layout-manager";
import { CareerPath, CareerSubPath } from "../job-info";

export class CourseLink {

  public isPrev: boolean = false;
  constructor(public subPath: CareerSubPath, public layoutManager: LayoutManager, public detailIndex: number) {
    this.isPrev = subPath.isPrev;
  }

  getPath() {

    let rects = this.layoutManager.rects;
    let fromRect = null;
    let toRect = null;

    rects.forEach(rectArray => {
      rectArray.forEach(rect => {
        if (rect.courseNode.name == this.subPath.from) {
          fromRect = rect;
        }

        if (rect.courseNode.name == this.subPath.to) {
          toRect = rect;
        }
      });
    });

    let sourceX = fromRect.getStartPos()[0];
    let sourceY = fromRect.getStartPos()[1];
    let targetX = toRect.getEndPos()[0];
    let targetY = toRect.getEndPos()[1];
    return "M" + sourceX + "," + sourceY
    + "C" + sourceX +  "," + (sourceY + targetY) / 2
    + " " + targetX + "," + (sourceY + targetY) / 2
    + " " + targetX + "," + targetY;
  }

  getArrowId(index: number) {
    return 'url(#arrow_' + index + ')';
  }

}
