import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { MajorInfo } from '../major-info';
import { StateService } from '../state.service';
import { RecommendService } from '../recommend.service';
import { CourseNode } from '../course-node';

@Component({
  selector: 'app-detail-panel',
  templateUrl: './detail-panel.component.html',
  styleUrls: ['./detail-panel.component.css']
})
export class DetailPanelComponent implements OnInit {

  currCourseNode: CourseNode;
  majorInfos: MajorInfo[] = [];
  selectedMajorInfo: MajorInfo;

  constructor(private stateService: StateService, private recommendService: RecommendService) {

  }

  ngOnInit() {

    //    initialize
    this.currCourseNode = this.recommendService.userCourseNode;
    this.recommendService.selectSchool(this.currCourseNode);

    this.recommendService.selectedCourseNodeObservable.subscribe(courseNode => {
      this.currCourseNode = courseNode;
      if (courseNode.layer == 4) {
        this.majorInfos = this.recommendService.getMajorInfos();
      } else {
        this.majorInfos = [];
        this.recommendService.selectSchool(this.currCourseNode);
      }
    });
  }

  clickBackBtn() {
    this.stateService.changeState(StateService.OVERVIEW);
    this.majorInfos = null;
    this.selectedMajorInfo = null;
  }

  selectMajorInfo(majorInfo: MajorInfo, index: number) {
    this.recommendService.selectMajorInfo(majorInfo, index);
    this.selectedMajorInfo = majorInfo;
  }
}

