import { courseInfoData } from "./data/course-info-data";

export class CourseNode {
  public courseInfo: CourseInfo;
  constructor(public layer: number, public name: string, public fromAge: number,
     public toAge: number) {
    this.courseInfo = CourseInfo.fromData(name);
  }
}

export class CourseInfo {

  constructor(public commonInfos: string[], public certificates: string[], public options: string[]) {
  }

  static fromData(courseName: string) {
    let currCourseInfo = courseInfoData[courseName];
    return new this(currCourseInfo.commonInfos, currCourseInfo.certificates, currCourseInfo.options);
  }
}
