import { Injectable } from '@angular/core';
import { CourseNode, CourseInfo } from './course-node';
import { Constants } from './constants';
import { Subject } from 'rxjs';
import { courseNodeData } from './data/course-node-data';

@Injectable({
  providedIn: 'root'
})
export class EduStructureService {

  private selectCourseNodeSubject = new Subject<CourseNode>();
  private currentArea = 'Berlin';

  public courseNodeObservable = this.selectCourseNodeSubject.asObservable();

  courseNodes: CourseNode[][] = courseNodeData["Overall"];

  constructor() {

  }

  applyArea(area: string) {
    let newData: CourseNode[][] = courseNodeData[area];
    this.courseNodes.forEach((nodes, layer) => {
      let newNodes: CourseNode[] = newData[layer];

      if (nodes.length > newNodes.length) {
        nodes.length = newNodes.length;
      }

      nodes.forEach((node, index) => {
        if (newNodes[index].name == node.name) {
          node.fromAge = newNodes[index].fromAge;
          node.toAge = newNodes[index].toAge;
        } else {
          nodes.splice(index, 1, newNodes[index]);
        }

      });

      if (nodes.length < newNodes.length) {
        for (let i = nodes.length; i < newNodes.length; i++) {
          nodes.push(newNodes[i]);
        }
      }
    });
    this.currentArea = area;
  }

  selectCourseNode(courseNode: CourseNode) {
    this.selectCourseNodeSubject.next(courseNode);
  }


}
