import { CourseLink } from "./graph/course-link";

export class JobInfo {

  constructor(public jobMetadata: JobMetadata, public paths: CareerPath[]) {
  }
}

export class CareerPath {
  paths: CareerSubPath[];
  currIndex: number;

  constructor(public name: string, public courses: string[], public currCourseName: string) {
    this.paths = this.getPaths();
    this.currIndex = this.getCurrentIndex();
  }

  getPaths() {
    let paths = [];
    let isPrev = true;
    for (let i = 0; i < this.courses.length - 1; i++) {
      if (this.courses[i] == this.currCourseName) {
        isPrev = false;
      }
      paths.push({from: this.courses[i], to: this.courses[i+1], isPrev: isPrev})
    }
    return paths;
  }

  getCurrentIndex() {
    return this.courses.findIndex(course => course == this.currCourseName);
  }

}

export class CareerSubPath {
  from: string;
  to: string;
  isPrev: boolean;
}

export class JobMetadata {
  name: string
  salary: string;
  balance: string;
  reviews?: string[];
  generalDescs?: string[];
  generalReviews?: string[];
}
