import { Injectable, ElementRef } from '@angular/core';
import * as d3 from 'd3'
import { CourseLink } from './graph/course-link';
import { LayoutManager } from './graph/layout-manager';
import { EduStructureService } from './edu-structure.service';
import { CareerPath, JobInfo } from './job-info';
import { Constants } from './constants';

@Injectable({
  providedIn: 'root'
})
export class D3Service {

  svg: any;
  layoutManager:LayoutManager;
  jobInfos: JobInfo[];

  links: CourseLink[] = [];
  constructor(private eduService: EduStructureService) {
    this.layoutManager = new LayoutManager(this.eduService.courseNodes);
  }

  private translate(x, y): string {
    return `translate(${x}, ${y})`;
  }

  applySVG(svgRef: ElementRef, width: number, height: number) {
    this.svg = d3.select(svgRef.nativeElement)
      .attr("width", width)
      .attr("height", height);
  }

  initLinks(paths: CareerPath[]) {
    paths.forEach((path, index) => {
      path.paths.forEach(subPath => {
          this.links.push(new CourseLink(subPath, this.layoutManager, index));
      });
    });

    this.svg.selectAll(".link")
      .data(this.links)
      .enter()
      .append("path")
      .attr("class", "link")
      .attr("d", (d) => d.getPath())
      .attr('fill', 'none')
      .attr('stroke-width', 3)
      .attr('stroke', (d) => d.isPrev ? 'green' : 'red');
  }

  deleteLinks() {
    this.links.length = 0;
    this.svg.selectAll('.link')
      .data(this.links)
      .exit()
      .remove();
  }

  updateLinks() {
    this.svg.selectAll(".link")
      .data(this.links)
      .transition(d3.transition().duration(400).ease(d3.easeLinear))
        .attr("d", d => d.getPath());
  }

  updateLinksWithNoTransition() {
    this.svg.selectAll(".link")
      .data(this.links)
      .attr("d", d => d.getPath());
  }

  trimLinks(detailIndex: number) {
    this.links = this.links.filter(link => detailIndex == link.detailIndex);
    this.svg.selectAll('.link')
    .data(this.links)
    .attr('stroke-width', 6)
    .style('opacity', 1)
    .exit()
    .remove();
    this.updateLinksWithNoTransition();
  }

  highlightLinks(pathIndex: number) {
    this.svg.selectAll(".link")
      .data(this.links)
      .attr("stroke-width", 2)
      .style("opacity", 0.5);

    this.svg.selectAll(".link")
      .data(this.links)
      .transition(d3.transition().duration(400))
        .attr("stroke-width", d => {
          if (d.detailIndex == pathIndex) {
            return 6;
          } else {
            return 2;
          }
        })
        .style("opacity", d => {
          if (d.detailIndex === pathIndex) {
            return 1;
          } else {
            return 0.5;
          }
        });
  }

  initYAxis() {
    let courseNodes = this.eduService.courseNodes;
    let minAge = 3
    let maxAge = 31
    let yScale = d3.scaleLinear().range([Constants.GRAPH_HEIGHT - Constants.GRAPH_MARGIN_BOTTOM, Constants.GRAPH_MARGIN_TOP])
      .domain([minAge, maxAge]);
    this.svg.append('g')
      .attr('class', 'axis')
      .style('font-size', 14)
      .attr("transform", this.translate(Constants.GRAPH_MARGIN_SIDE / 3, 0))
      .call(d3.axisRight(yScale));

    this.svg.append('text')
      .attr('class', 'legend')
      .attr("transform", this.translate(Constants.GRAPH_MARGIN_SIDE / 3, 5))
      .attr("dy", "1em")
      .style("text-anchor", "middle")
      .text("Age");
  }

  setYAxisMode(isDetail: boolean) {
    let rightMargin = isDetail ? Constants.GRAPH_MARGIN_SIDE / 3 : Constants.GRAPH_MARGIN_SIDE / 3;
    let minAge = 3
    let maxAge = 31

    let yScale = d3.scaleLinear().range([Constants.GRAPH_HEIGHT - Constants.GRAPH_MARGIN_BOTTOM, Constants.GRAPH_MARGIN_TOP])
      .domain([minAge, maxAge]);

    this.svg.select('g')
      .attr("transform", this.translate(rightMargin, 0))
      .call(d3.axisRight(yScale));

    this.svg.select('.legend')
      .attr("transform", this.translate(rightMargin, 10))
  }

  changeDetailMode(recommendResult: JobInfo, detailIndex: number) {
    let careerPath = recommendResult.paths[detailIndex];
    this.layoutManager.trimRects(careerPath);
    this.trimLinks(detailIndex);

    this.svg
      //.transition(d3.transition().duration(400).ease(d3.easeLinear))
      .attr('width', Constants.GRAPH_DETAIL_WIDTH);

    this.setYAxisMode(true);

  }

  changeOverviewMode(paths: CareerPath[], pathIndex: number) {
    this.layoutManager.generateRects();
    this.deleteLinks();
    this.initLinks(paths);
    this.highlightLinks(pathIndex);
    this.setYAxisMode(false);

    this.svg
      .attr('width', Constants.GRAPH_WIDTH);
  }
}
