import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { SchoolInfo } from '../school-info';
import { UserData } from '../user-data';
import { AreaService } from '../area.service';
import { } from '@types/googlemaps';
import { StateService } from '../state.service';
import { RecommendService } from '../recommend.service';

@Component({
  selector: 'app-map-view',
  templateUrl: './map-view.component.html',
  styleUrls: ['./map-view.component.css']
})
export class MapViewComponent implements OnInit {

  @ViewChild('gmap') gmapElement: any;
  @Input() userData: UserData;
  schoolInfos: SchoolInfo[] = [];

  map: google.maps.Map;
  homeMarker: google.maps.Marker;
  schoolMarkers: google.maps.Marker[] = [];
  selectedSchoolInfo: SchoolInfo;
  distance: string;
  duration: string;

  directionRenderer = new google.maps.DirectionsRenderer();

  //currAreaLatLng: LatLng;
  currState: number;
  constructor(private areaService: AreaService, private stateService: StateService,
    private recommendService: RecommendService) {

  }

  ngOnInit() {
    //this.currAreaLatLng = this.areaService.getCurrAreaLntLng();
    this.initGoogleMap();
  }

  initGoogleMap() {
    var mapProp = {
      zoom: 15,
      center: this.areaService.LNTLNGS[0]
    };
    this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp);
    google.maps.event.addListenerOnce(this.map, 'tilesloaded', () => {
      this.addUserHomeMarker(this.map)
        .then(homeMarker => {
          this.homeMarker = homeMarker;
          this.recommendService.schoolInfoObservable.subscribe(schoolInfos => {
            this.schoolInfos = schoolInfos;
            this.updateGoogleMap();
          });
        })
        .catch(error => console.log(error));
    });
  }

  updateGoogleMap() {
    this.setOnMarkers(null);
    this.homeMarker.setMap(this.map);
    this.schoolMarkers = [];
    var bounds = new google.maps.LatLngBounds();
    bounds.extend(this.homeMarker.getPosition());
    this.schoolInfos.forEach(schoolInfo => {
      var marker = new google.maps.Marker({
        map: this.map,
        position: schoolInfo.latlng,
        animation: google.maps.Animation.DROP,
        icon: 'https://maps.google.com/mapfiles/ms/icons/blue-dot.png'
      });
      bounds.extend(schoolInfo.latlng);
      marker.addListener('click', () => {
        this.selectedSchoolInfo = schoolInfo;
        let tempLatLng = new google.maps.LatLng(schoolInfo.latlng.lat, schoolInfo.latlng.lng);
        this.distance = (google.maps.geometry.spherical.computeDistanceBetween(tempLatLng, this.homeMarker.getPosition()) / 1000).toFixed(2);
        let directionService = new google.maps.DirectionsService();
        directionService.route({
          origin: this.homeMarker.getPosition(),
          destination: marker.getPosition(),
          travelMode: google.maps.TravelMode.TRANSIT
        }, (response, status) => {
          if (status == google.maps.DirectionsStatus.OK) {
            console.log(response);
            this.directionRenderer.setMap(this.map);
            this.directionRenderer.setDirections(response);
            this.duration = response.routes[0].legs[0].duration.text;
          } else {
            this.duration = "there are no appropriate routes";
          }
        });
      });
      this.schoolMarkers.push(marker);
      this.map.fitBounds(bounds);
      this.map.panToBounds(bounds);
    });
    //this.gmapElement.nativeElement.scrollIntoView({bahavior: 'smooth'});
  }

  addUserHomeMarker(map: google.maps.Map) {
    return new Promise<google.maps.Marker>((resolve, reject) => {
      var geocoder = new google.maps.Geocoder();
      //this.userData.address = 'Geeststrae 75, 40589 Dsseldorf'
      var address = this.userData.address;
      var marker: google.maps.Marker = new google.maps.Marker();
      geocoder.geocode({ 'address': address }, function (results, status) {
        if (status == google.maps.GeocoderStatus.OK) {
          map.setCenter(results[0].geometry.location);
          marker.setPosition(results[0].geometry.location);
          marker.setMap(map);
          marker.setAnimation(google.maps.Animation.DROP);
          resolve(marker);
        } else {
          reject(Error('Geocode was not successful for the following reason: ' + status));
        }
      });
    })
  }

  setOnMarkers(map) {
    this.homeMarker.setMap(map);
    this.schoolMarkers.forEach(marker => marker.setMap(map));
  }

}
