import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { JobInfo } from './job-info';

@Injectable({
  providedIn: 'root'
})

export class StateService {

  static INFO = 0;
  static OVERVIEW = 1;
  static DETAIL = 2;
  private stateSubject = new BehaviorSubject<number>(StateService.INFO);
  private recommendResultSubject = new Subject<JobInfo>();

  public stateObservable = this.stateSubject.asObservable();
  public recommendResultObservable = this.recommendResultSubject.asObservable();

  private currState: number = 0;
  constructor() { }

  changeState(nextState: number) {
    this.stateSubject.next(nextState);
  }

  applyRecommendResult(recommendResult: JobInfo) {
    this.recommendResultSubject.next(recommendResult);
  }

}
