import { Layer } from "./layer";
import { Rect } from "./rect";
import { CourseNode } from "../course-node";

import { CareerPath } from "../job-info";

export class LayoutManager {

  public rects: Rect[][] = [];
  public totalRectCount = 0;
  private layers: Layer[];
  private selectedCourseNodes: CourseNode[][] = [];

  constructor(public courseNodes: CourseNode[][]) {
    this.generateRects();
  }

  generateRects() {
    this.layers = [];
    this.courseNodes.forEach(arr => {
      let minAge = Math.min.apply(null, arr.map(node => node.fromAge));
      let maxAge = Math.max.apply(null, arr.map(node => node.toAge));
      this.layers.push(new Layer(minAge - 3, maxAge - 2, arr.length));
    });

    this.rects = [];
    this.courseNodes.forEach((arr, index) => {
      let layer = this.layers[index];
      let rectArray = [];
      arr.forEach((node, i) => {
        rectArray.push(new Rect(node, layer, i));
      });
      this.rects.push(rectArray);
    });
    this.totalRectCount = this.getTotalRectCount();
  }

  updateRects(isDetailMode: boolean) {
    let currCourseNodes = isDetailMode ? this.selectedCourseNodes : this.courseNodes;
    this.layers = [];
    currCourseNodes.forEach(arr => {
      let minAge = Math.min.apply(null, arr.map(node => node.fromAge));
      let maxAge = Math.max.apply(null, arr.map(node => node.toAge));
      this.layers.push(new Layer(minAge - 3, maxAge - 2, arr.length));
    });
    currCourseNodes.forEach((arr, index) => {
      let layer = this.layers[index];

      if (this.rects[index].length > arr.length) {
        this.rects[index].length = arr.length;
      }    

      this.rects[index].forEach((rect, i) => {
        rect.updateRect(arr[i], layer, i);
      });

      for (var i = this.rects[index].length; i < arr.length; i++) {
        this.rects[index].push(new Rect(arr[i], layer, i));
      }

    });
    this.totalRectCount = this.getTotalRectCount();
  }

  trimRects(careerPath: CareerPath) {
    careerPath.paths.forEach((path, index) => {
      this.rects[index] = this.rects[index].filter(rect => (rect.courseNode.name == path.from || rect.courseNode.name == path.to));
      this.rects[index + 1] = this.rects[index + 1].filter(rect => (rect.courseNode.name == path.from || rect.courseNode.name == path.to));
      this.rects[index][0].goToDetailMode();
      this.rects[index + 1][0].goToDetailMode();
    });

    this.totalRectCount = this.getTotalRectCount();
  }

  private getTotalRectCount() {
    let count = 0;
    this.rects.forEach(rectArr => {
      rectArr.forEach(rect => count++);
    });
    return count;
  }
}
